.CineDialog {
  background: var(--ui-gray-darkest);
  color: var(--text-secondary-color);
  filter: drop-shadow(0 0 3px var(--ui-gray-darkest));
  border: none;
  border-radius: 8px;
  width: inherit;
}
h5 {
  font-size: 20px;
  line-height: 35px;
  margin: 0;
}

h5,
label {
  font-weight: 400;
}
.cine-navigation,
.cine-controls,
.cine-options {
  cursor: default;
}
.fps-section input[type="range"] {
  background-color: transparent;
  border: 0 none;
  outline: 0 none;

  /* &::-ms-tooltip
        display: none */
}
.double-row-style {
  box-sizing: border-box;
  width: 290px;
  height: 70px;
  padding: 10px;
  top: 2%;
  left: 35%;
}
.cine-navigation {
  position: absolute;
  right: 16px;
  top: 10px;
}
.btn {
  padding: 0 4px;
}
